
.header {
  position: relative;
  overflow: hidden;
  color: white;
  width: 100%;
}
.header .content-wrapper {
    position: relative;
    padding: 20px 12px 17px 24px;
}
.header .content-wrapper .avatar {
      display: inline-block;
      border-radius: 4px;
}
.header .content-wrapper .content {
      display: inline-block;
      padding: 2px 0 2px 16px;
      line-height: 18px;
}
.header .content-wrapper .content .title {
        padding-bottom: 8px;
}
.header .content-wrapper .content .title .brand {
          display: inline-block;
          vertical-align: middle;
          width: 30px;
          height: 18px;
          background-image: url("brand@2x.png");
          background-size: 30px 18px;
          background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
              background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
          padding-left: 6px;
          font-size: 16px;
          font-weight: bold;
}
.header .content-wrapper .content .description {
        padding-bottom: 10px;
        font-size: 12px;
        font-weight: 200;
        line-height: 12px;
}
.header .content-wrapper .content .support {
        line-height: 12px;
}
.header .content-wrapper .content .support .icon {
          display: inline-block;
          vertical-align: middle;
          width: 12px;
          height: 12px;
          background-size: 12px 12px;
          background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
            background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.decrease {
                background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
            background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.discount {
                background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.guarantee {
            background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.guarantee {
                background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.invoice {
            background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.invoice {
                background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.special {
            background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.special {
                background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .support .text {
          font-size: 12px;
          font-weight: 200;
}
.header .content-wrapper .supports-count {
      position: absolute;
      right: 12px;
      bottom: 15px;
      padding: 2px 8px;
      border-radius: 18px;
      background-color: rgba(0, 0, 0, 0.2);
      text-align: center;
}
.header .content-wrapper .supports-count .count {
        font-size: 10px;
        font-weight: 200;
        line-height: 12px;
}
.header .content-wrapper .supports-count .icon-keyboard_arrow_right {
        line-height: 24px;
        font-size: 10px;
}
.header .bulletin-wrapper {
    position: relative;
    height: 28px;
    padding: 0 22px 0 12px;
    background-color: rgba(7, 17, 27, 0.2);
    line-height: 28px;
    font-size: 10px;
    font-weight: 200;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.header .bulletin-wrapper .icon {
      display: inline-block;
      vertical-align: middle;
      width: 22px;
      height: 12px;
      background-image: url("bulletin@2x.png");
      background-size: 22px 12px;
      background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .icon {
          background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      right: 12px;
      top: 8px;
}
.header .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    filter: blur(10px);
}



