html, body {
  line-height: 1;
  font-width: 200;
  font-family: "PingFang SC", "STHeitiSC-Light", "Helvetica-Light", arial, sans-serif; }

.clearfix {
  display: inline-block; }
  .clearfix:after {
    display: block;
    content: ".";
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden; }

@media (-webkit-min-device-pixel-ratio: 1.5) and (min-device-pixel-ratio: 1.5) {
