
.detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background: rgba(7, 17, 27, 0.8);
  opacity: 1;
}
.detail.fade-enter-active, .detail.fade-leave-active {
    transition: all 0.5s;
}
.detail.fade-enter, .detail.fade-leave-active {
    opacity: 0;
    background: rgba(7, 17, 27, 0);
}
.detail .detail-wrapper {
    width: 100%;
    min-height: 100%;
}
.detail .detail-wrapper .detail-main {
      margin-top: 64px;
      padding-bottom: 96px;
}
.detail .detail-wrapper .detail-main .name {
        line-height: 16px;
        text-align: center;
        font-size: 16px;
        font-weight: 700;
}
.detail .detail-wrapper .detail-main .star-wrapper {
        margin-top: 18px;
        padding: 2px 0;
        text-align: center;
}
.detail .detail-wrapper .detail-main .title {
        display: -ms-flexbox;
        display: flex;
        width: 80%;
        margin: 28px auto 24px auto;
}
.detail .detail-wrapper .detail-main .title .line {
          -ms-flex: 1;
              flex: 1;
          position: relative;
          top: -7px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.detail .detail-wrapper .detail-main .title .text {
          padding: 0 12px;
          font-weight: 700;
          font-size: 14px;
}
.detail .detail-wrapper .detail-main .supports {
        width: 80%;
        margin: 0 auto;
}
.detail .detail-wrapper .detail-main .supports .support-item {
          padding: 0 12px;
          margin-bottom: 12px;
          font-size: 0;
}
.detail .detail-wrapper .detail-main .supports .support-item:last-child {
            margin-bottom: 0;
}
.detail .detail-wrapper .detail-main .supports .support-item .icon {
            display: inline-block;
            width: 16px;
            height: 16px;
            vertical-align: top;
            margin-right: 6px;
            background-size: 16px 16px;
            background-repeat: no-repeat;
}
.detail .detail-wrapper .detail-main .supports .support-item .icon.decrease {
              background-image: url("decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.detail .detail-wrapper .detail-main .supports .support-item .icon.decrease {
                  background-image: url("decrease_2@3x.png");
}
}
