@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
                  background-image: url("guarantee_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
              background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3) and (min-device-pixel-ratio: 3) {
.detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
                  background-image: url("invoice_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .supports .support-item .icon.special {
              background-image: url("special_2@2x.png");
