}
.detail .detail-wrapper .detail-main .bulletin {
        width: 80%;
        margin: 0 auto;
        padding: 0 12px;
        line-height: 24px;
        font-size: 12px;
}
.detail .detail-close {
    position: relative;
    width: 32px;
    height: 32px;
    margin: -64px auto 0 auto;
    clear: both;
    font-size: 32px;
}



