
.navigation {
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  width: 100%;
  line-height: 40px;
  position: relative;
}
.navigation:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    content: ' ';
}
.navigation .nav-item {
    -ms-flex: 1;
        flex: 1;
    text-align: center;
}
.navigation .nav-item a {
      display: block;
      font-size: 14px;
      color: #4d555d;
}
.navigation .nav-item a.router-link-active {
        color: #f01414;
}



